import React, { Component } from "react";
import { withSiteData } from "react-static";
import { Container, Item, ProfileCard } from "../../components";

class HomeContainer extends Component {
  render() {
    return (
      <Container
        fullHeight
        justify="center"
        align="center"
        position="absolute"
        style={{ zIndex: 30 }}
      >
        <ProfileCard />
      </Container>
    );
  }
}

export default withSiteData(HomeContainer);



